textarea,
.docs {
  width: calc(100% - 10px);
  height: calc(100vh - 150px);
  border: none;
  outline: none;
  background-color: #121212;
  resize: none;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  color: #e3e3e3;
  padding: 5px;
  box-sizing: border-box;
  margin: 5px 5px 0px 5px;
  letter-spacing: 0.2px;
  font-family: monospace, sans-serif;
  font-size: 20px;
  text-align: left;
}

.docs {
  overflow-y: auto;
  overflow-x: hidden;
}
.docs .header {
  margin: 8px 0;
}
.docs .example {
  background-color: #3c3c3c;
  padding: 10px;
  border-radius: 4px;
  margin: 5px 0;
}

.docs h1 {
  font-size: 32px;
}
.docs h2 {
  font-size: 25px;
}
.docs h3 {
  font-size: 22px;
}
.docs p {
  font-size: 18px;
}
.docs small {
  font-size: 16px;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: #888;
}

::-webkit-scrollbar-thumb {
  background: #c7c7c7;
  border-radius: 4px;
  height: 60px;
  cursor: pointer;
}

::-webkit-scrollbar-thumb:hover {
  background: #f1f1f1;
}
